___CSS_LOADER_EXPORT___.push([module.id, `#payment-gateway-modal[data-test-id="payment-modal"] {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(15, 23, 42, 0.55);
  z-index: 9999;
  animation: fadeIn 180ms ease-out;
}

#payment-gateway-modal .modal-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

#payment-gateway-modal .modal-content {
  position: relative;
  width: min(480px, 92vw);
  height: min(640px, 88vh);
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 18px 48px rgba(0,0,0,0.18);
  overflow: hidden;
  transform: translateY(12px);
  animation: slideUp 200ms ease-out;
}

#payment-gateway-modal iframe[data-test-id="payment-iframe"] {
  border: none;
  width: 100%;
  height: 100%;
}

#payment-gateway-modal .close-button[data-test-id="close-modal-button"] {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #0f172a;
  color: #fff;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  line-height: 32px;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(24px); }
  to { transform: translateY(0); }
}

@media (max-width: 640px) {
  #payment-gateway-modal .modal-content {
    width: 96vw;
    height: 90vh;
    border-radius: 10px;
  }
}
