import { createModal, destroyModal } from './modal';

const ORIGIN_REGEX = /^https?:\/\//;

export default class PaymentGateway {
  constructor({ apiKey, checkoutBaseUrl, onSuccess, onFailure, onClose } = {}) {
    if (!apiKey) throw new Error('apiKey is required');
    if (!checkoutBaseUrl || !ORIGIN_REGEX.test(checkoutBaseUrl)) {
      throw new Error('checkoutBaseUrl must be a valid http/https URL');
    }
    const normalizedBase = checkoutBaseUrl.replace(/\/$/, '');
    const url = new URL(normalizedBase);

    this.apiKey = apiKey;
    this.checkoutBaseUrl = normalizedBase;
    this.checkoutOrigin = url.origin;
    this.onSuccess = onSuccess;
    this.onFailure = onFailure;
    this.onClose = onClose;
    this.modalOpen = false;
    this.messageHandler = this.handleMessage.bind(this);
  }

  open(params = {}) {
    if (this.modalOpen) return;
    const token = params.token;
    if (!token) throw new Error('token is required');

    const iframeSrc = `${this.checkoutBaseUrl}/checkout?token=${encodeURIComponent(token)}&key=${encodeURIComponent(this.apiKey)}`;
    createModal({ iframeSrc, onClose: this.handleClose.bind(this) });
    this.modalOpen = true;
    window.addEventListener('message', this.messageHandler);
  }

  close() {
    if (!this.modalOpen) return;
    this.cleanup();
    this.handleClose();
  }

  handleClose() {
    if (this.onClose) this.onClose();
  }

  handleMessage(event) {
    // Basic origin check to limit messages
    if (event.origin !== this.checkoutOrigin) return;

    const { type, payload } = event.data || {};
    if (!type) return;

    switch (type) {
      case 'payment_success':
        if (this.onSuccess) this.onSuccess(payload);
        this.cleanup();
        break;
      case 'payment_failed':
        if (this.onFailure) this.onFailure(payload);
        this.cleanup();
        break;
      case 'payment_closed':
        this.cleanup();
        if (this.onClose) this.onClose();
        break;
      default:
        break;
    }
  }

  cleanup() {
    destroyModal();
    window.removeEventListener('message', this.messageHandler);
    this.modalOpen = false;
  }
}
